/* eslint-disable no-console */
import { http } from './api'

export default {
  async validar (usuario, senha) {
    //senha = ''
    try {
    return await http.get(`PortalCliente/Login/${usuario}/${senha}`).then(resposta => 
        resposta.data[0]);
       // return dados;
    }
    catch(err) {
      alert('erro' + err)
    }
        
  }
}

