<template>
  <dx-validation-group>
    <div class="login-header">
      <div class="title">{{ title }}</div>
      <div><b>Acesse com seu login...</b></div>
    </div>

    <div class="dx-field">
      <dx-text-box placeholder="Nome" width="100%" :value.sync="login">
        <dx-validator>
          <dx-required-rule message="Nome obrigatrio" />
        </dx-validator>
      </dx-text-box>
    </div>

    <div class="dx-field">
      <dx-text-box
        placeholder="senha"
        width="100%"
        mode="password"
        :value.sync="password"
      >
        <dx-validator>
          <dx-required-rule message="Senha obrigatria" />
        </dx-validator>
      </dx-text-box>
    </div>

    <div class="dx-field">
      <dx-check-box :value.sync="rememberUser" text="Lembrar senha" />
    </div>

    <div class="dx-field">
      <dx-button
        type="default"
        text="Login"
        width="100%"
        @click="onLoginClick"
      />
    </div>

    <div class="dx-field" v-if="temD === true">
      <router-link to="/recovery"><a>Esqueceu a Senha ?</a></router-link>
    </div>

    <div class="dx-field" v-if="temD === true">
      <dx-button type="normal" 
                 text="Criar uma nova conta" width="100%"
                  @click="novaConta" />
    </div>
  <user-panel :usuario="usuario" 
  />
  </dx-validation-group>
</template>

<script>

import DxButton from "devextreme-vue/button";
import DxCheckBox from "devextreme-vue/check-box";
import DxTextBox from "devextreme-vue/text-box";
import DxValidationGroup from "devextreme-vue/validation-group";
import DxValidator, { DxRequiredRule } from "devextreme-vue/validator";

import auth from "../auth";

import validaUsuario from '../http/validaUsuario';
import validaCliente from '../http/validaCliente';
import validaFornecedor from '../http/validaFornecedor';
import validaDestinatario from '../http/destinatario';
//
import notify from 'devextreme/ui/notify';
//import usuario from '../app-user';

var dlogin        = {}
var dDestinatario = {}
var ivalidado     = 0;
var userLogin     = '';   
var userPassword  = '';

export default {
  data() {
    return {
      title: this.$appInfo.title,
      login: "",
      password: "",
      rememberUser: false,
      usuario: "",
      temD: false      
    }
  },
  created() {
    //this.$navigateTo("/home", { clearHistory: true });    

    this.login        = localStorage.login;    
    this.rememberUser = localStorage.rememberUser;
    this.password     = localStorage.password;

    //if ( !localStorage.rememberUser == true) {
    //   this.password = '';
   // }

    //Limpeza do LocalStorage
    localStorage.clear();
    //

   // if ( localStorage.rememberUser == true) {
   //   this.password = localStorage.password;
   // }
  },
  methods: {
      novaConta() {
        this.$router.push(this.$route.query.redirect || "/novoLogin");
      },
      //onCheckSenha(e) {
        //alert (e);
        //if (e.value==true) {
        //   this.password = localStorage.password;
       // }
      //},

      async onLoginClick(e) {

      if (!e.validationGroup.validate().isValid) {
        return;
      }
                
      ivalidado = 0;           
      
      localStorage.login        = this.login;
      localStorage.rememberUser = this.rememberUser;
      userLogin                 = this.login.toUpperCase();
      userPassword              = this.password.toUpperCase();

     // if (this.rememberUser == true) {
     //   localStorage.password = this.password;
     // }

      localStorage.usuario              = '';
      localStorage.cd_empresa           = 0;
      localStorage.cd_tipo_destinatario = 0;
      localStorage.cd_destinatario      = 0;
      localStorage.nm_destinatario      = '';
      localStorage.empresa              = '';
      localStorage.fantasia             = '';
      localStorage.email                = '';
      localStorage.cd_modulo            = 0;
      localStorage.cd_menu              = 0;
      localStorage.cd_usuario           = 0;
      localStorage.cd_cliente           = 0;
      localStorage.cd_fornecedor        = 0;
      localStorage.cd_api               = 0;
      localStorage.cd_tipo_consulta     = 0;
      localStorage.dt_base              = new Date().toLocaleDateString();
      //localStorage.dt_inicial           = new Date().toLocaleDateString();
      //localStorage.dt_final             = new Date().toLocaleDateString();
      localStorage.cd_identificacao     = 0;
      localStorage.nm_documento         = '';
      localStorage.cd_modulo_selecao    = 0;
      localStorage.nm_modulo            = '';
      localStorage.password             = '';

      //
      var date = new Date();

      localStorage.dt_inicial = new Date(date.getFullYear(), date.getMonth(), 1).toLocaleDateString('en-us');
      localStorage.dt_final   = new Date(date.getFullYear(), date.getMonth() + 1, 0).toLocaleDateString('en-us');
      //
      //alert(typeof(localStorage.dt_inicial));
           // alert(localStorage.dt_base);

      //dlogin = validaUsuario.validar(this.login, this.password);
      dlogin = await validaCliente.validar(this.login, this.password);

      if (dlogin.nm_usuario_email == userLogin && 
          dlogin.cd_senha_acesso  == userPassword) {

          ivalidado = 1;

      }
     // else {
     //   notify('Usurio no Localizado !', 'error', 1000); 
     // }

      //alert(ivalidado);

      //Fornecedor
      if (ivalidado == 0) {
        
          //Validar o Fornecedor
          dlogin = await validaFornecedor.validar(this.login, this.password);

          //alert(dlogin.nm_fantasia_usuario);

          if (dlogin.nm_usuario_email == userLogin && 
              dlogin.cd_senha_acesso  == userPassword) {    
            
              ivalidado = 1;

             }
          //else {
          //  notify('Usurio no Localizado !', 'error', 1000); 
         // }

      }
      
     // alert(ivalidado);

      if (ivalidado == 0) {

         //Validar Usurio

         dlogin = await validaUsuario.validar(this.login, this.password);

         //alert(dlogin.nm_fantasia_usuario);
        // alert(dlogin.cd_senha_acesso);
        // alert(this.login);
        // alert(this.password);
         

         if (((dlogin.nm_fantasia_usuario == userLogin)    && 
              (dlogin.cd_senha_acesso     == userPassword) ) ||
            ((dlogin.nm_usuario_email     == userLogin)    && 
             (dlogin.cd_senha_acesso      == userPassword) ))             

          {            
            ivalidado = 1;
          }
          else {
          //alert('1');
           if (ivalidado == 0) {      
              notify('Usurio no Localizado !', 'error', 1000); 
           }

          }

       }   //

       if (ivalidado == 0) {
           //alert('2');
            notify('Usurio no Localizado !', 'error', 1000);        
       }
       else {
             //Storage no Browser
             //
             localStorage.usuario              = dlogin.nm_fantasia_usuario;
             localStorage.cd_empresa           = dlogin.cd_empresa;
             localStorage.cd_tipo_destinatario = dlogin.cd_tipo_destinatario;             
             localStorage.cd_destinatario      = dlogin.cd_destinatario;
             localStorage.empresa              = dlogin.nm_empresa;
             localStorage.fantasia             = dlogin.nm_fantasia_empresa
             localStorage.email                = dlogin.nm_usuario_email;
             localStorage.cd_modulo            = dlogin.cd_modulo;
             localStorage.cd_usuario           = dlogin.cd_usuario;
             localStorage.cd_cliente           = dlogin.cd_cliente;
             localStorage.cd_fornecedor        = dlogin.cd_fornecedor;
             localStorage.cd_api               = dlogin.cd_api;
             localStorage.nm_modulo            = dlogin.nm_modulo;             
             localStorage.password             = dlogin.cd_senha_acesso;
             localStorage.cd_tipo_consulta     = 0;
             localStorage.nm_destinatario      = '';
             localStorage.cd_identificacao     = 0;
             localStorage.cd_parametro         = 0;
             localStorage.nm_documento         = '';
             localStorage.cd_modulo_selecao    = 0;

            // if ( !this.rememberUser == true) {
            //    localStorage.password             = '';
            // }   

             if (dlogin.cd_tipo_destinatario == 0) {
               localStorage.cd_cliente         = 0;
             }            
             
             //alert(dlogin.cd_modulo);

             if (dlogin.cd_destinatario>0 ) {

                dDestinatario = await validaDestinatario.getDestinatario(
                dlogin.cd_empresa, dlogin.cd_tipo_destinatario, dlogin.cd_destinatario
                );

                //alert(dDestinatario[0].nm_fantasia);
                //console.log(dDestinatario[0].nm_fantasia);

                //if (!dDestinatario[0].nm_fantasia == undefined) {
                   localStorage.nm_destinatario = dDestinatario[0].nm_fantasia;
               // }
             }

             //----------------------------------------------------------------------------------- 
             //Autenticao do Usurio
             //
             auth.logIn();
             //
             
             //alert('login');

             //this.$router.navigateTo(this.$route.query.navigateTo || "/home", { clearHistory: true });
             //this.$router.go(this.$route.query.redirect || "/home");
             this.$router.push(this.$route.query.redirect || "/home",  { clearHistory: true });
             //this.$router.replace(this.$route.query.redirect || "/home",  { clearHistory: true });
             //
             
             //
             e.validationGroup.reset();
             ////

          }                              
    }

  },

  components: {
    DxButton,
    DxCheckBox,
    DxTextBox,
    DxValidator,
    DxRequiredRule,
    DxValidationGroup    
  }
}
</script>

<style lang="scss">
@import "../themes/generated/variables.base.scss";

.login-header {
  text-align: center;
  margin-bottom: 40px;

  .title {
    color: $base-accent;
    font-weight: bold;
    font-size: 30px;
    margin: 0;
  }
}
</style>
