/* eslint-disable no-console */
import { http } from './api'

export default {
  async validar (usuario, senha) {
    //senha = ''
    try {
    return await http.get(`Login/Usuario/0/${usuario}/${senha}`).then(resposta => 
        resposta.data[0]);
       // return dados;
    }
    catch(err) {
      alert('erro' + err)
    }   
  }
  }